.product-descr {
	padding-block: 67px 69px;
	padding-inline-end: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
	padding-inline-start: clamp(1.875rem, 4.5vw + 0.7rem, 6.063rem);

	background: #fff;

	h2 {
		font-size: 48px;
		font-family: Gilroy;
		font-weight: 700;
		line-height: 110%;

		color: #4f4f4f;

		text-align: left;
	}

	p {
		font-size: 24px;
		font-weight: 500;
		line-height: 150%;
		color: #333;

		&.product-descr-red {
			font-size: 18px;
			font-weight: 500;
			color: #f12e6d;
		}
	}

	h3 {
		margin: 0;
		margin-block-start: 64px;
		font-size: 48px;
		font-weight: 700;
		line-height: 110%;
		color: #4f4f4f;
		text-align: left;
	}
}
