.tips {
	position: relative;
	&__label {
		position: absolute;
		z-index: 2;
		inset-block-start: -40px;
		padding: 30px 25px 22px 30px;

		width: 182px;
		height: 122px;

		border-radius: 30px;

		font-size: 32px;
		font-weight: 700;
		line-height: 110%;

		background: #ff9900;
		color: #e8edf0;
	}

	&__wrapper {
		display: grid;
		grid-template:
			'. tip1 tip2 tip3'
			/ 86px 1fr 1fr 1fr;
		gap: 61px;

		margin-block-start: 89px;
	}

	&__tip {
		position: relative;

		padding: 42px 35px 35px 51px;
		background: #fff;
		border-radius: 30px;

		font-size: 24px;
		font-weight: 500;
		line-height: 150%;

		p {
			position: relative;
			z-index: 10;
			margin: 0;
		}

		&::after {
			position: absolute;
			inset-block-end: -10px;

			display: block;
			content: '';
			height: 104px;
		}

		&:first-child {
			grid-area: tip1;

			&::after {
				inset-inline-end: 44px;

				width: 47px;

				background: url(../../../img/svg/tips-1.svg) center center/cover
					no-repeat;
			}
		}

		&:nth-child(2) {
			grid-area: tip2;

			&::after {
				inset-inline-end: 26px;

				width: 70px;

				background: url(../../../img/svg/tips-2.svg) center center/cover
					no-repeat;
			}
		}

		&:last-child {
			grid-area: tip3;

			&::after {
				inset-inline-end: 31px;

				width: 73px;

				background: url(../../../img/svg/tips-3.svg) center center/cover
					no-repeat;
			}
		}
	}
}
