.choice {
	display: block;
	padding-block: 142px 91px;

	h2 {
		font-weight: 800;
		font-size: 48px;
		line-height: 110%;

		color: #4f4f4f;

		text-transform: none;
		text-align: left;
	}

	p {
		margin-block-start: 47px;

		font-weight: 600;
		font-size: 24px;
		line-height: 150%;
		letter-spacing: normal;

		color: #333333;
	}

	&-row {
		display: flex;
		gap: 50px;
	}

	&-map {
		svg {
			width: 554px;
			height: 289.66px;
		}
	}
}
