.product-content {
	.gardex-reviews {
		display: flex;
		flex-direction: column;
	}

	.related-products {
		flex-direction: column;

		h4 {
			margin: 85px 0 0 0;

			text-align: left;
			color: #4f4f4f;

			font-size: 48px;
			font-style: normal;
			font-weight: 700;
			line-height: 110%;
		}

		&-list {
			margin-block-start: 87px;
		}

		&-product {
			&-data {
				p {
					font-size: 30px;
					font-style: normal;
					font-weight: 500;
					line-height: 110%;
				}
			}
		}
		display: flex;
	}

	.lines-list {
		margin-block-start: 99px;
		p {
			display: none;
		}

		&-item {
			&:first-child {
				padding-inline-start: 63.9px;
				padding-inline-end: 30px;
			}

			svg {
				max-width: 97.56px;
				max-height: 70.25px;
			}
		}
	}
}
