.production {
	padding-block: 100px;

	&-filters {
		display: flex;
		justify-content: end;
		gap: 127px;

		margin-block-end: 48px;

		&__item {
			cursor: pointer;

			&:first-child {
				color: #f2c94c;
				border-bottom-color: #f2c94c;
			}
		}
	}

	&-row {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: 20px 0;

		&:not(:last-child) {
			margin-block-end: 51px;
		}
	}

	&-item {
		display: flex;
		flex-direction: column;
		max-height: 443px;
		max-width: 221px;
		min-width: 221px;

		img {
			margin: 0 auto;
			width: revert;
			height: auto;
			max-height: 223px;
			object-fit: none;
		}

		form {
			max-width: 204px;

			button {
				min-width: 204px;
			}
		}
	}

	&-name {
		margin-block-start: 20px;

		font-weight: 600;
		font-size: 23px;
		letter-spacing: normal;
		line-height: 110%;

		color: #333333;
	}

	&-actions {
		form {
			margin-block-start: 24.41px;
			max-width: revert;
			width: 100%;

			button {
				max-width: revert;
				width: 100%;
				height: 39.37px;
			}

			&:hover {
				.arrow {
					animation: spin-production 0.5s ease-out;
					animation-fill-mode: forwards;
				}
			}
		}

		#slider {
			position: relative;

			padding-block-start: 50px;

			.slider {
				&-navigation {
					position: absolute;

					inset-block-start: 22px;
					inset-inline-end: 0;
					min-height: 15px;
				}

				&-button-prev {
					left: -65px;
					&::after {
						font-size: 15px;
					}
				}

				&-button-next {
					&::after {
						font-size: 15px;
					}
				}

				&-button-prev,
				&-button-next {
					color: #e8edf0;
				}

				img {
					margin-block: 0;
				}

				&-title {
					position: absolute;

					inset-block-start: 15px;

					font-weight: 600;
					font-size: 12.6px;
					line-height: 150%;

					color: #4f4f4f;
				}
			}
		}

		a {
			img {
				height: 39.39px;
				width: 39.39px;
				margin-block: 0;
			}
		}
	}

	.button {
		margin-top: 24px;
		width: 100%;

		&:hover {
			background: #a2d45e;

			.button__arrow {
				transform: translateX(170px) rotate(180deg);
			}
		}
	}

	&-poster {
		width: 100%;
		img {
			height: 100%;
			max-height: revert;
			object-fit: none;
		}
	}
}

