.gardex-awards {
	padding-block: 70px 76px;
	&-row {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: 50px;
	}

	&-item {
		img {
			width: auto;
			height: 100px;
		}
	}

	&-years {
		margin-block-start: 28px;

		font-weight: 900;
		font-size: 40px;
		line-height: 110%;
		/* identical to box height, or 44px */

		text-transform: uppercase;

		color: #333333;
	}

	&-title {
		margin-block-start: 6px;

		font-weight: 700;
		font-size: 18px;
		line-height: 110%;
		/* identical to box height, or 20px */

		text-transform: uppercase;

		color: #f2c94c;
	}
}
