.my-slider {
	position: relative;

	&-body {
		position: relative;
		width: 100%;
		min-height: 989px;
		margin: 0 auto;
		overflow: hidden;
	}

	.slider {
		&-item {
			position: absolute;

			display: flex;
			align-items: end;
			padding-block-end: 201px;

			width: 100%;
			height: 100%;

			opacity: 0;
			padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
			transition: opacity 0.5s;

			background: url(../../../img/background-family-product.png) center
				top/cover no-repeat;

			&.active {
				opacity: 1;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	.controls {
		position: absolute;
		inset-block-end: 64px;

		display: flex;
		gap: clamp(1rem, 15.7vw - 10.8rem, 8.063rem);

		text-align: center;

		padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
	}

	.pagination {
		gap: 20px;
		li {
			width: 10px;
			height: 10px;
			background: #f2f2f2;
			border-radius: 50%;

			&.active {
				height: 17px;
				width: 17px;

				background: transparent;
				border: 4px solid #f2f2f2;
			}
		}
	}

	ul {
		padding: 0;
		margin: 0;
		display: flex;
		gap: clamp(0.1rem, 6.7vw - 4.9rem, 3.125rem);
	}

	li {
		cursor: pointer;
		display: inline-block;
		margin: 0 5px;

		a {
			border: none;
		}

		&:before {
			display: none;
		}
	}

	.nav {
		li {
			font-weight: 700;
			font-size: 18px;
			line-height: 110%;
			/* identical to box height, or 22px */

			display: flex;
			align-items: flex-end;
			text-transform: uppercase;

			/* Gray 6 */
			a {
				color: #f2f2f2;
				mix-blend-mode: overlay;
			}

			&.active {
				a {
					color: #fff;
					mix-blend-mode: normal;
				}
			}
		}
	}
}

@media screen and (max-width: 1024px) {
	.my-slider {
		.slider {
			&-item {
				background: url(../../../img/bg-family-mob.png) center top/cover
					no-repeat;
			}
		}

		.controls {
			.pagination {
				display: none;
			}
		}
	}

	.main-social {
		display: none;
	}
}

@media screen and (max-width: 950px) {
	.my-slider {
		.controls {
			display: none;
		}
	}
}
