.awards {
	padding-block-start: 96px;
	padding-block-end: 55px;

	h3 {
		font-weight: 900;
		font-size: 40px;
		line-height: 120%;

		text-transform: uppercase;

		color: #333333;
	}

	&-row {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(205px, 1fr));
	}
	&-item {
		display: grid;
		gap: 0 29px;
	}
	&-logo {
		img {
			height: auto;
		}
	}
	&-badge {
		text-align: center;
		&__name {
			font-weight: 700;
			font-size: 24px;
			line-height: 120%;

			text-transform: uppercase;

			color: #000000;
			white-space: nowrap;
		}
		&__descr {
			font-weight: 600;
			font-size: 16px;
			line-height: 120%;

			text-transform: uppercase;

			color: #df1125;
		}
	}
}
