.announcements {
	padding-block: 85px 152px;

	h5 {
		font-weight: 900;
		font-size: 40px;
		line-height: 120%;

		text-align: center;
		text-transform: uppercase;

		color: #333333;
	}

	&-container {
		background: url(../../../img/svg/announcements-bg.svg) center center/cover
			no-repeat;
	}

	&-row {
		grid-template-columns: minmax(551px, 1fr) repeat(
				auto-fit,
				minmax(253px, 1fr)
			);
		gap: 50px;

		margin-block-start: 85px;
	}

	&-item {
		position: relative;

		.button {
			position: absolute;
			right: 0;

			display: flex;
			align-items: center;
			justify-content: center;

			margin-top: 35px;

			background: #4b7816;
			border-radius: 14.5px 16px 0px 14.5px;

			width: 151px;

			&__arrow {
				display: none;
			}

			a {
				margin: 0;
				right: revert;

				animation: none;
			}

			&:hover {
				a {
					animation: none;
				}
			}
		}

		&:first-child {
			.announcements {
				&-img {
					position: relative;

					border: 8px solid #000000;
					border-radius: 20px;
					&__title {
						position: absolute;
						inset-block-end: 20px;
						inset-inline-start: 20px;

						font-size: 48px;
						font-weight: 800;
						line-height: 90%;

						color: #f2f2f2;
					}
				}
			}
			img {
				margin: 0;
				height: 185px;
			}
		}
		&:not(:first-child) {
			.announcements {
				&-img {
					height: 285px;
					padding-block: 20px;
					background: #fff;
					box-shadow: 10px 10px 24px rgba(0, 0, 0, 0.15);
					border-radius: 20px;
					img {
						margin: 0;
						margin-inline-start: 20px;
						height: 115px;

						border-radius: 10px;
					}
				}

				&-text {
					position: absolute;
					inset-block-end: 20px;
					margin-inline: 20px;

					display: flex;
					flex-direction: column;
					gap: 31px;

					width: 87%;

					p {
						align-items: start;
						margin: 0;

						font-weight: 900;
						font-size: 20px;
						line-height: 120%;
						letter-spacing: 1px;
						text-transform: uppercase;
					}

					a {
						align-self: center;

						color: #ecbe4c;
						border-color: #ecbe4c;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 1360px) {
	.announcements {
		&-row {
			grid-template-columns: 1fr 1fr;
			grid-template-rows: 1fr 1fr 1fr;
			grid-template-areas:
				'main main'
				'. .'
				'. .';
		}

		&-item {
			&:first-child {
				grid-area: main;
			}
		}
	}
}
