.by-online {
	position: relative;
	width: 100%;
	margin: 0 auto;
	overflow: hidden;

	h3 {
		margin: 0;

		text-align: left;
		text-transform: none;
		font-weight: 800;
		font-size: 48px;
		line-height: 110%;

		color: #4f4f4f;
	}

	.slider-container {
		display: flex;
		transition: all 0.5s ease;
	}

	.slide {
		display: flex;
		align-items: end;
		flex-shrink: 0;
		gap: 80px;
		width: 100%;
		height: 132px;

		svg {
			width: 100%;
			height: 60px;
		}
	}

	.slider-arrows {
		position: absolute;
		top: 24px;
		right: 50px;

		display: flex;
		justify-content: space-between;
		gap: 23px;

		transform: translateY(-50%);
	}

	.slider-arrow {
		height: 50px;
		width: 50px;

		cursor: pointer;

		background: none;
		border: none;

		font-size: 24px;
		color: #fff;

		&.right {
			background: url(../../../img/svg/arrow-circled-right.svg) center
				center/contain no-repeat;
		}

		&.left {
			background: url(../../../img/svg/arrow-circled-left.svg) center
				center/contain no-repeat;
		}
	}

	.slider-pagination {
		position: absolute;
		top: 0;
		right: 10px;

		font-weight: 700;
		font-size: 20px;
		line-height: 100%;
		display: flex;
		align-items: start;
		gap: 7px;
		text-align: center;
		color: #ecbe4c;

		.total-pages {
			position: relative;
			font-size: 10px;
			height: auto;
			line-height: 1;

			&::before {
				position: absolute;
				bottom: -3px;
				right: 0;

				display: block;
				content: '';
				background: #ecbe4c;

				height: 4px;
				width: 4px;
				border-radius: 100%;
			}
		}
	}
}
