.second-header {
	position: relative;
	display: grid;
	grid-template-columns: 104px 25px 1fr;
	align-items: center;
	padding-block: 51px 42px;
	gap: 47px;

	&-menu {
		justify-self: end;
		&__list {
			display: flex;
			gap: 0 100px;
			padding-inline-start: 0;
			margin-block: 0;
		}

		&__item,
		&__search {
			display: flex;
			align-items: center;
			color: #333;
			font-size: 20px;
			font-weight: 600;

			&::before {
				display: none;
			}

			a {
				display: block;
				padding: 10px;
				border-color: transparent;

				color: #fff;
				mix-blend-mode: difference;

				transition: all $transition;

				&:hover {
					border-color: $accent-color;
				}
			}
		}
	}
}

.logo {
	z-index: 20;
	width: 104px;
	border-bottom: 0;
	svg {
		width: 104px;
		height: 97px;
	}
}

.search {
	z-index: 20;
	width: 17px;
	height: 17px;

	cursor: pointer;

	&-mob {
		display: none;
	}
}

@media screen and (max-width: 1135px) {
	.second-header {
		grid-template-columns: 20px auto 20px;
		grid-template-areas: 'burger logo search-mob';
		&-menu {
			display: none;
		}
	}

	.logo {
		grid-area: logo;
		justify-self: center;
	}

	.search {
		&-mob {
			display: block;
			justify-self: end;
		}
	}
}

@media screen and (max-width: 1441px) {
	.second-header {
		&-menu {
			&__list {
				gap: 0 30px;
			}
		}
	}
}
