.map {
	position: relative;
	iframe {
		border-radius: 20px;
	}

	.show-more {
		position: absolute;
		inset-block-start: -30px;
		inset-inline-start: 52px;

		width: 551px;
		&__item {
			max-width: 551px;
			padding-block: 42px;
			padding-inline: 49px;

			border-radius: 20px;

			background: #fff;

			&:not(:first-child) {
				margin-block-start: 28px;
			}
		}
		&__button {
			font-weight: 700;
			font-size: 24px;
			line-height: 120%;

			color: #333333;

			text-transform: uppercase;

			text-align: left;

			&::after {
				// inset-inline-end: -131px;
				inset-inline-end: -213px;
				border: none;

				width: 50px;
				height: 50px;

				background: url(../../../img/svg/arrow-circled-down.svg) center
					center/contain no-repeat;

				transform: translateY(-40%) rotate(0);
			}

			&--active::after {
				transform: translateY(-50%) rotate(-180deg);
			}
		}
	}
}
