.supfooter {
	display: grid;
	grid-template-columns: auto 1fr;
	align-items: center;
	gap: 30px;

	padding-block: 60px;

	svg {
		width: auto;
		max-height: 69px;
	}

	&-companies {
		display: grid;
		grid-template-columns: repeat(9, 1fr);
		gap: 20px;
		img {
			margin: 0;
			max-height: 69px;
			object-fit: contain;
		}
	}
}
