.gardex-reviews {
	position: relative;
	display: flex;
	align-items: end;
	gap: 46px;

	padding-block: 51px;

	overflow-x: scroll;

	&-body {
		display: flex;
		gap: 45px;
	}

	&-logo {
		position: sticky;
		inset-inline-start: 0;
		padding-block: 51px 61px;
		border-radius: 20px;
		background: #fff;
		z-index: 1;

		svg {
			min-width: 568px;
			min-height: 320px;
		}
	}

	&-item {
		position: relative;
		height: 270px;
		width: 662px;
		padding: 39px 35px;

		display: flex;
		flex-direction: column;
		justify-content: space-between;

		border-radius: 20px;

		background: #fff;

		&__avatar {
			position: absolute;
			inset-block-start: -45px;
			inset-inline-start: 33px;
			width: 132px;

			img {
				height: 132px;
				object-fit: contain;
			}
		}

		p {
			margin: 0;
		}
	}

	&-heading {
		display: flex;
		align-items: start;
		justify-content: end;
		gap: 132px;

		svg {
			width: 154px;
			height: 28px;
		}

		&__date {
			font-weight: 600;
			font-size: 18px;
			line-height: 22px;
			/* identical to box height */

			/* Gray 4 */

			color: #bdbdbd;
		}
	}
}
