.burger {
	display: block;

	width: 25px;
	height: 20px;
	cursor: pointer;

	&__inner {
		cursor: pointer;
		position: relative;
		z-index: 1000;
		inset-block-start: 0;
		inset-inline-end: 0;
		height: 100%;

		&:focus {
			border: 1px solid $base-color;
		}

		div {
			display: block;
			position: absolute;
			z-index: 2;

			width: 100%;
			height: 5px;
			background-color: #f2c94c;

			&:nth-child(1) {
				inset-block-start: 0;
				transition: transform $transition;
			}

			&:nth-child(2) {
				inset-block-start: 50%;
				transform: translateY(-50%);
				transition: transform $transition;
			}

			&:nth-child(3) {
				inset-block-end: 0;
				transition: transform $transition;
			}
		}

		&--active {
			div {
				&:nth-child(1) {
					inset-block-start: 7px;
					transform: rotate(45deg);
				}

				&:nth-child(2) {
					transform: scale(0);
					opacity: 0;
				}

				&:nth-child(3) {
					inset-block-end: 8px;
					transform: rotate(-45deg);
				}
			}
		}
	}
}

@media screen and (max-width: 1135px) {
	.burger {
		position: static;
		grid-area: burger;
	}
	.layout-none {
		.burger {
			inset-inline-end: $layout-margin-mob;
		}
	}
}
