.score {
	padding-block-start: 85px;
	font-weight: 900;
	font-size: clamp(1.75rem, 1.7vw + 0.5rem, 2.5rem);
	line-height: 120%;

	text-transform: uppercase;

	&-row {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 64px;
	}

	&-point {
		position: relative;

		&__number {
			fill: #fff;
			height: 285.6px;
		}
		&:first-child {
			.score-point__number {
				width: 129px;
			}
		}

		&:nth-child(2) {
			.score-point__number {
				width: 193.2px;
			}
		}

		&:last-child {
			.score-point__number {
				width: 200px;
			}
		}
	}

	p {
		position: absolute;
		bottom: 10px;
		margin: 0;
	}
}

@media screen and (max-width: 950px) {
	.score {
		&-row {
			grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
		}

		p {
			bottom: revert;
			top: 50%;
			transform: translateY(-50%);
		}
	}
}

@media screen and (max-width: 768) {
	.score {
		p {
			font-size: 1.75rem;
		}
	}
}
