.product-selection {
	&-sections {
		display: flex;
		flex-direction: row;
		align-items: center;

		font-weight: 600;

		margin-inline-start: -30px;

		padding-block: 100px;
	}

	&-navigation {
		&__item {
			display: flex;
			gap: 50px;

			padding: 17px 30px;

			font-weight: 800;
			font-size: 20px;
			line-height: 110%;
			/* identical to box height, or 22px */
			text-transform: uppercase;

			/* Gray 1 */

			color: #333333;

			transition: all 0.3s ease-in-out;

			&.active,
			&:hover {
				padding: 17px 30px;

				background: #e8edf0;
				border-radius: 70px;

				cursor: pointer;
			}
		}
	}

	&-wrapper {
		padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
	}

	&-line {
		max-width: 641px;
		padding-block-start: 197px;
		padding-block-end: 47px;

		div {
			font-weight: 700;
			font-size: 20px;
			line-height: 110%;
			/* identical to box height, or 22px */
			text-transform: uppercase;

			color: #f2c94c;
		}

		h3 {
			padding: 3px;
			margin: 0;

			font-weight: 700;
			font-size: 73px;
			line-height: 110%;
			/* or 80px */
			text-transform: uppercase;

			color: #e8edf0;
		}

		p {
			font-weight: 500;
			font-size: 18px;
			text-transform: none;
			line-height: 150%;
			/* or 27px */

			color: #ffffff;
		}

		&__descr {
			margin-block-start: 20px;
		}

		&__buttons {
			display: flex;
			gap: 20px;

			margin-block-start: 53px;

			a {
				border: none;

				&:first-child {
					padding: 13px 20px 10px;

					width: 206px;
					height: 39px;

					background: #4b0505;
					border-radius: 10px;

					color: #e01c1c;

					text-transform: none;

					font-weight: 500;
					font-size: 16px;
					line-height: 100%;
					/* identical to box height, or 16px */
				}

				&:last-child {
					padding: 13px 20px 10px;

					width: 198px;
					height: 39px;

					background: #f2c94c;
					border-radius: 10px;

					color: #e01c1c;

					text-transform: none;
					font-weight: 500;
					font-size: 16px;
					line-height: 100%;
					/* identical to box height, or 16px */
				}
			}
		}
	}

	&-slider {
		overflow-x: hidden;
		position: relative;

		.swiper-slide {
			&.green {
				background: url(../../../img/product-selection-family.png) center
					center/cover no-repeat;
			}
			&.red {
				background: url(../../../img/product-selection-extreme.png) #e1251b;
				background-position: 0 47px;
				background-repeat: no-repeat;
			}
			&.yellow {
				background: url(../../../img/product-selection-baby.png) center
					center/cover no-repeat;
			}
			&.lightgreen {
				background: url(../../../img/product-selection-natural.png) center
					center/cover no-repeat;
			}
			&.tiffany {
				background: url(../../../img/product-selection-therapy.png) center
					center/cover no-repeat;
			}
		}

		.pagination {
			position: absolute;
			inset-block-start: 113px;
			inset-inline-start: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
			margin-inline-start: -30px;
			z-index: 2;

			display: flex;
			flex-direction: row;
			justify-content: start;
			align-items: center;

			gap: 0;

			height: 50px;

			font-weight: 600;

			.swiper-pagination-bullet {
				width: auto;
				height: auto;
				padding: 17px 30px;

				font-weight: 800;
				font-size: 20px;
				line-height: 110%;
				/* identical to box height, or 22px */
				text-transform: uppercase;

				/* Gray 1 */

				color: #333;

				border-radius: 70px;
				background: none;

				opacity: 0.8;

				transition: all 0.3s ease-in-out;

				&:first-child {
					&.swiper-pagination-bullet-active {
						color: #91cf50;
					}
				}

				&:nth-child(2) {
					&.swiper-pagination-bullet-active {
						color: #f2c94c;
					}
				}

				&:nth-child(3) {
					&.swiper-pagination-bullet-active {
						color: red;
					}
				}

				&:nth-child(4) {
					&.swiper-pagination-bullet-active {
						color: #bedf9c;
					}
				}

				&:last-child {
					&.swiper-pagination-bullet-active {
						color: #23c2b6;
					}
				}

				&.swiper-pagination-bullet-active,
				&:hover {
					padding: 17px 30px;

					background: #e8edf0;
					border-radius: 70px;

					opacity: 1;

					cursor: pointer;
				}
			}
		}
	}

	&-extreme-slider {
		position: relative;
		overflow: hidden;

		margin-block-start: 49px;

		padding-inline: 50px;

		.swiper {
			padding-bottom: 42px;
			&-wrapper {
				padding-bottom: 47px;
			}

			&-slide {
				width: 100%;
				img {
					height: 135px;
					object-fit: contain;
				}
			}

			&-button-prev {
				left: 0;
				background: url(../../../img/svg/arrow-left.svg) center center/cover
					no-repeat;

				&::after {
					display: none;
				}
			}

			&-button-next {
				right: 0;
				background: url(../../../img/svg/arrow-right.svg) center center/cover
					no-repeat;

				&::after {
					display: none;
				}
			}

			&-pagination {
				&-fraction {
					width: auto;

					left: revert;
					right: 55px;
					bottom: 4px;

					font-size: 0;

					span {
						font-weight: 800;
						line-height: 100%;
						/* identical to box height, or 10px */

						color: #ffffff;
					}
				}

				&-current {
					font-size: 20px;
				}

				&-total {
					position: relative;
					font-size: 10px;

					inset-block-start: -8px;

					&:after {
						position: absolute;

						inset-block-end: -4px;
						inset-inline-end: 2px;

						display: block;
						content: '';
						background-color: #fff;

						height: 4px;
						width: 4px;
					}
				}
			}

			&-scrollbar {
				width: 50%;
				left: 28%;
				bottom: 11px;
				height: 1px;
				background: black;

				&-drag {
					width: 20%;
					top: -1px;
					height: 4px;
					background: #fff;
				}
			}
		}
	}
}
