.useful-articles {
	&-heading {
		display: flex;
		justify-content: space-between;

		h5 {
			margin: 0;

			font-weight: 700;
			font-size: 48px;
			text-transform: none;
			line-height: 110%;
			/* or 53px */

			/* Gray 1 */

			color: #333333;
		}

		div {
			font-weight: 500;
			font-size: 24px;
			line-height: 150%;
			/* identical to box height, or 36px */

			text-decoration-line: underline;

			color: #74bc1f;
		}
	}

	&-row {
		display: grid;
		grid-template-columns: 2fr 1fr 1fr;
		gap: 51px;

		margin-block-start: 17px;
		margin-block-end: 69px;
	}

	&-main {
		display: flex;
		flex-direction: column;
		justify-content: end;

		padding: 50px;

		background: url(../../../img/useful-articles-main.png) center center/cover
			no-repeat;
		color: #fff;

		border-radius: 30px;

		h6 {
			margin: 0;

			text-align: left;

			font-weight: 700;
			font-size: 48px;
			text-transform: none;
			line-height: 110%;
			/* or 53px */

			color: #e8edf0;
		}

		p {
			font-weight: 500;
			font-size: 24px;
			line-height: 150%;
			/* or 36px */

			color: #e8edf0;
		}
	}

	&-item {
		border-radius: 30px;
		color: #fff;
		background: #00364c;

		&:last-child {
			background: #41ab34;
		}

		img {
			border-radius: 30px 30px 0 0;
		}

		div {
			padding: 17px 31px 20px 32px;
		}
	}
}

@media screen and (max-width: 1650px) {
	.useful-articles {
		&-row {
			grid-template-columns: 1fr 1fr;
			grid-template-rows: repeat(auto-fit, minmax(355px, 1fr));
			grid-template-areas:
				'main main'
				'. .';
		}

		&-main {
			&:first-child {
				grid-area: main;
			}
		}
	}
}
