.about-gardex {
	position: relative;
	display: flex;
	align-items: end;
	// min-height: 1085px;

	&-bg {
		position: absolute;
		inset-block-start: 0;
		inset-inline-start: 0;
		width: 100%;
		display: block;
		img {
			height: 100%;
		}
	}
	&-content {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 50px;
		padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
		padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
		margin-block-start: clamp(14.813rem, 24.6vw + 8.2rem, 37.75rem);

		p {
			align-self: end;
			line-height: 150%;
			margin-block-end: 80px;
		}

		&__video {
			position: relative;
			img {
				position: relative;
				border: 5px solid #000;
				border-radius: 50px;
				width: 100%;
				height: 392px;
				&::before {
					position: absolute;
					inset-block-start: 50%;
					inset-inline-start: 50%;
					display: block;
					width: 57px;
					height: 40px;
					content: '';
					background: url(../../../img/svg/youtube.svg) center center/cover
						no-repeat;
					transform: translate(-50%, -50%);
				}
			}
			&::before {
				position: absolute;
				z-index: -1;
				inset-block-end: 20px;
				inset-inline-start: -280px;
				display: block;
				content: '';
				width: 498.85px;
				height: 19.22px;
				background: url(../../../img/svg/wave.svg) center center/cover no-repeat;
			}
			&::after {
				position: absolute;
				z-index: -1;
				inset-block-end: 60px;
				inset-inline-end: -60px;
				width: 99px;
				height: 99px;
				display: block;
				content: '';
				background: url(../../../img/svg/bubble.svg) center center/cover
					no-repeat;
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.about-gardex {
		p {
			// margin-block-start: 100px;
		}

		&-content {
			grid-template-columns: 0.7fr 1fr;
		}
	}
}

@media screen and (max-width: 1200px) {
	.about-gardex {
		p {
			margin-block-start: 100px;
		}

		&-content {
			grid-template-columns: 1fr;
		}
	}
}

@media screen and (max-width: 768px) {
	.about-gardex {
		p {
			margin-block-start: 50px;
		}
	}
}
