.button {
	position: relative;
	z-index: 10;

	background: #f2994a;
	width: 260px;
	height: 50px;
	border-radius: 30px;

	cursor: pointer;

	transition: background 0.5s ease-in-out;

	&:hover {
		background: #a2d45e;

		a {
			animation: transform 0.3s ease;
			animation-fill-mode: forwards;
		}

		.button__arrow {
			transform: translateX(210px) rotate(180deg);
		}
	}

	a {
		position: absolute;
		top: 28%;
		right: 0;

		margin: 0 29px;

		font-weight: 500;
		font-size: 20px;
		line-height: 100%;
		letter-spacing: 0.03em;

		text-decoration: none;
		border: 0;

		color: #fff;

		transition: all 0.3s ease-in-out;

		animation: revert-transform 0.5s ease-out;
		animation-direction: reverse;
	}

	&__arrow {
		position: absolute;
		inset-block-start: 0;
		inset-inline-start: 0;
		z-index: 1;

		display: flex;
		justify-content: center;
		align-items: center;

		background: #ecbe4c;
		height: 50px;
		width: 50px;
		border-radius: 100%;

		transition: transform 0.5s ease-in-out;

		div {
			border-top: 3px solid #fff;
			border-right: 3px solid #fff;

			height: 5px;
			width: 5px;

			transform: rotate(45deg);
		}
	}
}

@keyframes transform {
	0% {
		right: 0;
	}

	100% {
		right: 20%;
	}
}

@keyframes revert-transform {
	0% {
		right: 0;
	}

	100% {
		right: 20%;
	}
}
