*,
*:before,
*:after {
	box-sizing: border-box;
}

body {
	font-family: $font-regular;
	font-size: clamp(1rem, 0.7vw + 0.7rem, 1.125rem);
	line-height: 150%;
	/* or 27px */
	font-weight: 500;
	letter-spacing: normal;

	color: $base-color;
	background-color: $gray;

	overflow-x: hidden;
}

::selection {
	background: rgba(189, 189, 189, 0.54);
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $font-h;
	letter-spacing: normal;
	line-height: normal;
}

h1 {
	font-size: clamp(1.75rem, 1vw + 1.3rem, 2.5rem);
}

h2,
h3,
h4,
h5,
h6 {
	font-size: 1.5rem;
}

p {
	margin-block: $ind;
	font-family: $font-p;
}

ul {
	margin-block: $ind;
	padding-inline-start: $ind;
}

li {
	list-style: none;

	&:before {
		display: inline-block;
		content: '';
		position: relative;
		left: -10px;
		top: -3px;
		height: 3px;
		width: 3px;
		background-color: $base-color;
		border-radius: 100%;
	}
}

a {
	text-decoration: none;
	color: #91cf50;
	border-bottom: 1px solid #91cf50;
	transition: color $transition;

	:visited {
		color: rgba(51, 51, 51, 0.66);
	}

	&:hover {
		color: $accent-color;
	}
}

blockquote {
	margin-block: $ind;
	margin-inline: 0;
	line-height: normal;
}

// section,
// aside,
// article {
// 	padding-block: 83px;
// 	padding-inline: 150px;
// }

section,
aside,
article {
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		text-align: center;
		text-transform: uppercase;
	}
}

pre {
	font-family: inherit;
	font-size: inherit;
	font-weight: inherit;
	margin: 0;
}

.row {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
	gap: $gap;

	&__content {
		display: flex;
		gap: $ind;
		align-items: center;
		justify-content: flex-start;
	}
}

.content {
	display: grid;
	grid-template-columns: minmax(200px, 1fr);
	gap: $gap;
}

.content-sidebar {
	display: grid;
	grid-template-columns: minmax(350px, auto) 1fr;
	gap: 0 $gap;
	height: 100%;
}

.background {
	position: relative;
	background: url(../../img/background-default.png) center center/cover
			no-repeat,
		#5fa713;
}

// Класс выключено

.disabled {
	pointer-events: none;
	color: $disabled;
}

// Класс вступительного текста

.introductory {
	font-family: serif;
	line-height: normal;
	padding-block-end: $ind;
	border-bottom: $border;
}

// Стиль страницы 404

.not-available {
	position: absolute;
	inset-block-start: 50%;
	inset-inline-start: 50%;
	transform: translate(-50%, -50%);

	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	gap: 124px;

	width: 100%;
	padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);

	&__img {
		max-width: 706px;

		img {
			object-fit: contain;
		}
	}

	&-container {
		position: relative;

		height: 100vh;
	}

	&-prev {
		text-align: right;
	}

	&-next {
		text-align: left;
	}

	&-prev,
	&-next {
		a {
			border: none;
			svg {
				height: 28.95px;
			}
		}
	}

	&__bg {
		position: absolute;
		inset-block-end: 0;
		inset-inline-start: 0;

		width: 100%;
	}
}

@media screen and (max-width: 1135px) {
	.container {
		&__small {
			margin-inline: 8vw;
		}
	}
}

@media screen and (max-width: 768px) {
	.row {
		&__content {
			flex-wrap: wrap;
		}
	}
}

@media screen and (max-width: 375px) {
	.row {
		grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
	}
}
