.footer {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 20px;
	align-items: center;

	padding-block-start: 63px;
	padding-block-end: 142px;

	background: url(../../../img/svg/footer-bg.svg) center center/cover no-repeat;

	font-size: 1rem;
	font-size: 0.7rem;
	.logo {
		svg {
			height: 32px;
		}
	}
	.social {
		display: flex;
		gap: 35px;
		svg {
			width: 35px;
			height: 35px;
		}
	}

	.upeco {
		.logo {
			width: 119px;
			height: 32px;
		}
		.contacts {
			font-weight: 600;
			font-size: 16px;
			line-height: 150%;
			color: #015ac2;
		}
	}

	nav {
		display: flex;
		flex-direction: column;
		gap: 30px;
		font-size: 1.25rem;
		ul {
			display: flex;
			gap: 20px;
			margin-block: 0;
			padding-inline-start: 0;
		}
		li {
			a {
				color: #333;

				border-color: #333;
			}

			&:before {
				display: none;
			}
		}
	}
}

#footer-index {
	grid-template-columns: 1fr 1fr;
	gap: 0;

	background: gray;

	font-weight: 600;
	font-size: 16px;
	line-height: 120%;
	/* or 19px */

	.upeco {
		color: #2e7a9b;
	}
}
