.expert-replies {
	display: grid;
	grid-template-columns: auto 636px;

	gap: 64px;

	&-container {
		position: relative;
		background-color: #91cf50;

		padding-inline-end: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
	}

	&-bg {
		display: flex;
		align-items: end;
		justify-content: start;

		img {
			// height: 615px;
			height: auto;
			object-fit: contain;
			object-position: right;
		}
	}

	&-faq {
		padding-block: 86px 102px;
		max-width: 636px;

		color: #fff;
	}

	&-button {
		display: flex;
		align-items: center;
		justify-content: start;

		margin-top: 32px;

		max-width: 340px;
		height: auto;

		background: #fff;
		border-radius: 16px 0px 16px 16px;

		font-size: 20px;
		line-height: 100%;
		text-transform: lowercase;
		/* identical to box height, or 20px */

		cursor: pointer;

		a {
			display: block;
			margin: 0;
			padding: 15px 13px 15px 20px;

			font-weight: 300;

			border: none;

			color: #333;
		}

		&__arrow {
			div {
				height: 13px;
				width: 13px;

				border-top: 5px solid #333;
				border-right: 5px solid #333;

				transform: rotate(45deg);
			}
		}
	}
}

