.container {
	position: relative;
	padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);

	&-small {
		padding-inline: clamp(1.875rem, 3.4vw + 1rem, 5rem);
	}

	&-first {
		padding-inline-start: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
		padding-inline-end: clamp(1.875rem, 3.4vw + 1rem, 5rem);
	}

	&-white {
		padding-inline-start: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
		padding-inline-end: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
		padding-block-start: 3.125rem;
		background: #fff;
	}

	&-green {
		padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);

		background: #91cf50;
	}

	&-scroll {
		padding-inline-start: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
	}

	&-footer {
		background: #fff;
		padding-inline: clamp(1.875rem, 3.4vw + 1rem, 5rem);
	}

	&-aboutgardex {
		padding-inline-start: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
		padding-inline-end: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
	}

	&-product {
		position: absolute;
		inset-block-start: 0;

		display: grid;
		grid-template-columns: 40.83% 59.06%;

		width: 100%;
	}
}
