.gardex-about {
	display: block;

	margin-block: 50px 70px;

	h2 {
		font-weight: 700;
		font-size: 48px;
		text-align: left;
		text-transform: none;
		line-height: 110%;
		/* identical to box height, or 53px */

		color: #4f4f4f;
	}

	&-lines {
		&__row {
			grid-template-columns: repeat(auto-fill, minmax(221px, 1fr));
			gap: 87px;
		}

		&__item {
			img {
				height: auto;
			}
		}
	}
}
