.product-instructions {
	display: grid;
	gap: 18px 150px;

	padding-block-start: 51px;
	padding-block-end: 91px;
	padding-inline-end: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
	padding-inline-start: clamp(1.875rem, 4.5vw + 0.7rem, 6.063rem);

	background: #333;

	p {
		font-size: 15px;
		font-weight: 700;
		line-height: 110%;
		text-transform: uppercase;

		color: #b4f4ac;
	}

	&-row {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
		gap: 57px 150px;
	}

	&-descr {
		font-size: 18px;
		font-weight: 500;
		color: #f2f2f2;
	}

	&-number {
		position: relative;

		&__number {
			font-size: 150px;
			font-weight: 800;
			line-height: 120%;
			text-transform: uppercase;
			color: #f2c94c;
		}

		&__stage {
			position: absolute;
			inset-block-end: 40px;
			inset-inline-start: 50px;

			font-size: 40px;
			font-family: Gilroy;
			font-weight: 700;
			line-height: 120%;
			text-transform: uppercase;
			color: #fff;
		}
	}
}
