.announce {
	margin-block: 50px 41px;
	&-question {
		font-weight: 600;
		font-size: 24px;
		line-height: 150%;

		color: #ecbe4c;
		&__text {
			font-weight: 800;
			font-size: 48px;
			line-height: 110%;
			/* or 53px */

			color: #333333;
		}
	}
	&-answer {
		font-weight: 600;
		font-size: 24px;
		line-height: 150%;
		/* identical to box height, or 36px */

		color: #ecbe4c;

		&__text {
			font-weight: 600;
			font-size: 24px;
			line-height: 150%;
			/* or 36px */

			color: #333333;
		}
	}

	&-readmore {
		font-weight: 600;
		font-size: 24px;
		line-height: 150%;
		/* identical to box height, or 36px */

		text-decoration-line: underline;

		color: #74bc1f;
	}
}
