.main {
	padding-block-end: 201px;
	margin-block-start: 73px;
	margin-block-end: 50px;

	&-row {
		position: relative;
		display: grid;
		grid-template-columns: 37.45% auto;
		gap: 26px;
		height: 100%;
	}
	&-text {
		max-width: 603px;
		color: #fff;

		h1 {
			margin: 0;
			text-transform: uppercase;
		}

		p {
			margin-block-start: 25px;

			font-weight: 600;
			font-size: clamp(1.125rem, 0.8vw + 0.5rem, 1.5rem);
			line-height: 150%;
		}

		.button__container {
			margin-block-start: 94px;
		}
	}
	&-product {
		img {
			width: 100%;
			max-width: 226px;
			height: 100%;
			max-height: 520px;
		}
	}
	&-useful {
		position: absolute;
		inset-block-end: 50px;
		inset-inline-end: clamp(1.875rem, 3.4vw + 1rem, 5rem);

		display: flex;
		flex-direction: column;
		gap: 22.73px;

		&-item {
			display: grid;
			grid-template-rows: 1fr 24px;
			color: #fff;
			&__img {
				position: relative;
				width: 351px;

				img {
					height: 185px;
					border: 10px solid #fff;
					border-top-right-radius: 41.83px;
					border-bottom-left-radius: 41.83px;
				}

				div {
					position: absolute;
					inset-block-end: 30px;
					inset-inline-end: 20px;
					font-weight: 800;
					font-size: clamp(1.082rem, 1.2vw + 0.6rem, 2rem);
					line-height: 150%;
				}
			}

			&:nth-child(2) {
				.main-useful-item__img {
					&::before {
						position: absolute;
						inset-block-start: 50%;
						inset-inline-start: 50%;
						transform: translate(-50%, -50%);

						height: 40.66px;
						width: 37.03px;

						display: block;
						content: '';

						background: url(../../../img/svg/play.svg) center center/cover
							no-repeat;

						cursor: pointer;
					}
				}
			}

			&__descr {
				display: flex;
				justify-content: end;
				font-weight: 400;

				text-transform: lowercase;
			}

			&:last-child {
				.main-useful-item__img {
					& > div {
						font-weight: 800;
						line-height: 150%;

						color: #e8edf0;

						mix-blend-mode: normal;
					}
				}
			}
		}
	}
	&-social {
		position: absolute;
		inset-block-end: 64px;
		inset-inline-start: 80px;
		z-index: 1;

		mix-blend-mode: overlay;
		&__names {
			display: flex;
			flex-direction: row;
			align-items: center;
			gap: 20px;
			min-height: 398px;
			writing-mode: vertical-lr;
			padding-inline-end: 72px;

			font-weight: 600;
			text-transform: lowercase;
			color: #fff;
			a {
				display: inline-block;
				text-orientation: mixed;
				unicode-bidi: plaintext;
				margin-block: 0;
				border: none;
				transform: rotate(-180deg);
			}
		}
		&__year {
			font-weight: 600;
			color: #ffffff95;
		}
	}
	&-sections {
		position: absolute;
		display: flex;
		flex-direction: row;
		gap: 50px;
		inset-block-end: 64px;
		color: #fff;
		font-weight: 600;
	}

	&-navigation {
		&__item {
			font-weight: 800;
			font-size: 20px;
			line-height: 110%;
			/* identical to box height, or 22px */

			display: flex;
			align-items: flex-end;
			text-transform: uppercase;

			color: #f2f2f2;
		}
	}
	h1 {
		font-size: clamp(2.25rem, 9.3vw - 1.8rem, 4rem);

		font-weight: 800;
		font-size: 64px;
		line-height: 110%;
		/* or 70px */

		color: #f2f2f2;
	}
}

@media screen and (max-width: 1780px) {
	.main {
		&-text {
			max-width: 450px;
		}
	}
}

@media screen and (max-width: 1441px) {
	.main {
		&-row {
			grid-template-columns: 32.45% auto;
		}
		&-useful {
			&-item {
				&__img {
					div {
						font-size: 0.9rem;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 1441px) {
	.main {
		&-text {
			max-width: 350px;
		}
		&-useful {
			&-item {
				&__img {
					div {
						font-size: 24.5419px;
						line-height: 150%;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 1200px) {
	.main {
		&-useful {
			inset-block-end: revert;
			inset-block-start: 50%;
			transform: translateY(-50%);
			&-item {
				&__img {
					width: 187.67px;
					height: auto;

					border-radius: 16.225px 0px 16.225px 16.225px;

					img {
						border-width: 6.49px;

						height: 100px;
					}

					div {
						font-size: 17.3066px;
						/* identical to box height, or 26px */

						inset-block-end: 10px;
						inset-inline-end: 20px;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 950px) {
	.main {
		&-text {
			max-width: 368px;
		}
	}
}
