.advantages {
	background-color: #fff;

	&-row {
		gap: 50px;
	}

	&__heading {
		display: grid;
		grid-template-columns: auto 1fr;
		align-items: end;
		gap: 30px;

		&:nth-of-type(1) {
			h4 {
				margin: 0;

				color: #56ccf2;
				text-align: left;
			}
			svg {
				max-width: 80.13px;
				height: 83px;
			}
		}
	}

	&__descr {
		line-height: 150%;
		/* or 27px */

		color: #333333;
	}
}
