.article-main {
	padding-block: 151px 85px;
	color: #fff;
	h1 {
		text-transform: uppercase;

		font-weight: 800;
		font-size: 64px;
		line-height: 110%;
		/* or 70px */

		text-transform: uppercase;

		color: #fff;
	}
	p {
		max-width: 692px;
	}

	.arrow {
		background: #2f80ed;
	}

	.button {
		width: 389px;

		background: #2d9cdb;

		&__arrow {
			background: #2f80ed;
		}

		&:hover {
			background: #a2d45e;
			.button__arrow {
				background: #658b32;

				transform: translateX(339px) rotate(180deg);
			}
		}
	}
}

.article-container {
	background: url(../../../img/svg/bg-article.svg) center center/cover no-repeat;
}
