.pagination {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 123px;

	margin-block-end: 52px;

	svg {
		width: 50px;
		height: 50px;
	}

	&__numbers {
		display: flex;
		align-items: center;
		gap: 56px;

		div {
			font-size: 32px;
			font-weight: 700;
			line-height: 120%;
			/* or 38px */

			color: #bdbdbd;

			transition: margin 0.3s ease-out;
		}

		.active {
			margin-block-start: -10px;
			font-size: 40px;
			line-height: 120%;
			font-weight: 700;
			/* identical to box height, or 48px */

			color: #828282;
		}
	}
}
