.line-extreme {
	position: relative;

	padding-block: 87px 0;
	min-height: 490px;

	color: #fff;

	h1 {
		text-transform: uppercase;
	}

	p {
		max-width: 692px;
	}

	&-nav {
		position: absolute;
		z-index: 2;
		inset-block-end: -70px;

		ul {
			display: flex;
			gap: 20px;

			padding: 0;
		}

		li {
			font-weight: 700;
			font-size: 20px;
			line-height: 110%;
			text-transform: uppercase;

			color: #bdbdbd;

			&::before {
				display: none;
			}

			a {
				color: inherit;
				border: none;
			}
		}

		.active {
			color: #e1251b;
		}
	}
}
