.article-content {
	margin-block-start: 50px;

	&-date {
		font-weight: 600;
		font-size: 24px;
		line-height: 150%;

		color: #ecbe4c;

		&__text {
			font-weight: 800;
			font-size: 48px;
			line-height: 110%;
			/* or 53px */

			color: #333333;
		}
	}

	h1,
	h2,
	h3,
	h4 {
		margin: 0;

		font-weight: 700;
		font-size: 48px;
		line-height: 110%;
		/* or 53px */

		/* Gray 1 */

		color: #333333;
	}

	p {
		font-weight: 500;
		font-size: 24px;
		line-height: 150%;
		/* or 36px */

		color: #333333;
	}

	&__grid {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
		grid-template-rows: 1fr 1fr;
		gap: 21.94px;
		grid-template-areas:
			'big-picture big-picture . . .'
			'big-picture big-picture . . .';
		min-height: 311px;

		&--bigpicture {
			grid-area: big-picture;

			background: url(../../../img/article-content-grid-1.png) center
					center/cover no-repeat,
				#d9d9d9;
			padding: 0 32px;

			border-radius: 30px;

			.plus {
				&-main {
					position: absolute;
					inset-block-end: 21px;
					inset-inline-end: 21px;

					width: 48.242px;
					height: 48.242px;

					background: url(../../../img/svg/plus.svg) center center/cover
						no-repeat;
				}
			}
		}

		div {
			position: relative;

			border-radius: 13.95px;

			img {
				height: 144.57px;
				margin: 0;

				border-radius: 30px;
			}

			&:first-child {
				img {
					height: 311px;
					&:hover {
						object-position: 120%;
					}
				}
			}

			&:nth-child(2) {
				background: url(../../../img/article-content-grid-2.png) center
					center/cover no-repeat;
			}

			&:nth-child(3) {
				background: url(../../../img/article-content-grid-3.png) center
					center/cover no-repeat;
			}

			&:nth-child(4) {
				background: url(../../../img/article-content-grid-4.png) center
					center/cover no-repeat;
			}

			&:nth-child(5) {
				background: url(../../../img/article-content-grid-5.png) center
					center/cover no-repeat;
			}

			&:nth-child(6) {
				background: url(../../../img/article-content-grid-6.png) center
					center/cover no-repeat;
			}

			&:nth-child(7) {
				background: url(../../../img/article-content-grid-5.png) center
					center/cover no-repeat;
			}

			.plus {
				position: absolute;
				inset-block-end: 9.3px;
				inset-inline-end: 9.3px;

				width: 23.243px;
				height: 23.243px;

				background: url(../../../img/svg/plus.svg) center center/cover no-repeat;
			}
		}
	}
}
