.lines-list {
	display: grid;
	grid-template-columns: auto auto auto auto auto;

	&-item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-block: 68px 47px;
		padding-inline: 30px;

		&:first-child {
			padding-inline-start: clamp(1.875rem, 6.7vw + 0.1rem, 8.125rem);
			padding-inline-end: 30px;
			background: #41ab34;
		}

		&:nth-child(2) {
			background: #e1251b;
		}

		&:nth-child(3) {
			background: #86ca73;
		}

		&:nth-child(4) {
			background: #fce200;
		}

		&:last-child {
			padding-inline-end: clamp(1.875rem, 6.7vw + 0.1rem, 8.125rem);
			padding-inline-start: 30px;
			background: #00cac9;
		}

		svg {
			width: 151px;
			margin: 0 auto;
		}
	}
}
