.line-extreme-products {
	margin-block-start: 75px;
	margin-block-end: 143px;

	&-heading {
		display: flex;
		justify-content: space-between;

		&__descr {
			font-weight: 500;
			font-size: 24px;
			line-height: 150%;

			color: #333333;
		}
	}

	&-list {
		display: grid;
		gap: 100px 0;

		margin-block-start: 102px;
	}

	&-product {
		display: grid;
		grid-template-columns: 180px auto 280px;
		background: #fff;

		border-radius: 200px 30px 30px 200px;

		padding: 20px;

		gap: 0 30px;

		&__img {
			position: relative;
			div {
				position: relative;
				width: 180px;
				height: 180px;
				border-radius: 50%;

				background: #f2f2f2;
				filter: blur(7.5px);
			}

			img {
				position: absolute;
				inset-block-start: 50%;
				inset-inline-start: 50%;
				z-index: 100;

				height: auto;
				width: auto;

				object-fit: none;

				transform: scale(100%) translate(-50%, -50%);
			}
		}

		&-data {
			p {
				font-weight: 600;
				font-size: 30px;
				line-height: 110%;

				color: #333333;
			}
		}

		&-features {
			display: flex;
			gap: 0 70px;

			margin-block-start: 25px;

			letter-spacing: normal;

			&-group {
				&__line {
					display: flex;
					justify-content: space-between;

					min-width: 300px;

					font-weight: 600;
					font-size: 18px;
					line-height: 150%;

					color: #4f4f4f;

					background: url(../../../img/line.png) center center/contain no-repeat;

					div {
						background: #fff;
					}
				}
			}
		}

		&__actions {
			padding-block-start: 10px;

			form {
				max-width: revert;
				width: 100%;

				button {
					max-width: revert;
					width: 100%;
				}

				&:hover {
					.arrow {
						animation: spin-product 0.5s ease-out;
						animation-fill-mode: forwards;
					}
				}
			}

			#slider {
				position: relative;

				padding-block-start: 50px;

				.slider {
					&-navigation {
						position: absolute;

						inset-block-start: 22px;
						inset-inline-end: 0;
						min-height: 15px;
					}

					&-button-prev {
						left: -65px;
						&::after {
							font-size: 15px;
						}
					}

					&-button-next {
						&::after {
							font-size: 15px;
						}
					}

					&-button-prev,
					&-button-next {
						color: #e8edf0;
					}

					img {
						margin-block: 0;
					}

					&-title {
						position: absolute;

						inset-block-start: 19px;

						font-weight: 600;
						font-size: 16px;
						line-height: 150%;

						color: #4f4f4f;
					}
				}
			}

			a {
				img {
					height: 50px;
					width: 50px;
					margin-block: 0;
				}
			}
		}
	}
}
@keyframes spin-product {
	0% {
		transform: rotate(0) translateX(0);
	}
	100% {
		transform: translateX(230px) rotate(90deg);
	}
}
