.choice-banner {
	display: flex;
	flex-direction: column;
	justify-content: end;
	align-items: safe;

	min-height: 355px;
	margin-block: 92px 69px;

	padding: 0 50px 37px;

	border-radius: 30px;

	background: url(../../../img/choice-banner.png) center center/cover no-repeat;

	h6 {
		margin: 0;

		font-weight: 700;
		font-size: 48px;
		line-height: 110%;
		/* or 53px */
		text-align: left;
		text-transform: none;

		color: #e8edf0;
	}

	p {
		margin-block-start: 18px;
		margin-block-end: 0;

		font-weight: 600;
		font-size: 24px;
		line-height: 150%;
		/* or 36px */

		color: #e8edf0;
	}

	&__content {
		max-width: 600px;
	}
}
