.gardex-about-video {
	position: relative;
	display: flex;
	justify-content: center;
	img {
		position: relative;
		z-index: 5;
		display: inline-block;
		border: 5px solid #000;
		border-radius: 50px;
		width: 774.44px;
		height: 435.56px;
		margin: 17px auto 63px;

		&::before {
			position: absolute;
			inset-block-start: 50%;
			inset-inline-start: 50%;
			transform: translate(-50%, -50%);
			z-index: 2;

			display: block;
			content: '';
			width: 57px;
			height: 40px;
			content: '';
			background: url(../../../img/svg/youtube.svg) center center/contain
				no-repeat;
		}
	}
	&::before {
		position: absolute;
		z-index: 1;
		inset-block-start: 120px;
		inset-inline-end: 250px;
		display: block;
		content: '';
		width: 498.85px;
		height: 19.22px;
		background: url(../../../img/svg/wave.svg) center center/cover no-repeat;
	}
	&::after {
		position: absolute;
		z-index: 1;
		inset-block-end: 120px;
		inset-inline-start: 300px;
		width: 99px;
		height: 99px;
		display: block;
		content: '';
		background: url(../../../img/svg/bubble.svg) center center/cover no-repeat;
	}
}
