.expert-tabs {
	&__tab {
		background: #91cf50;
	}

	&__heading {
		display: flex;
		justify-content: space-between;
		position: relative;
		z-index: 1;
		outline: none;
		padding-inline-end: $ind;
		padding-block: 20px;
		border-bottom: 2px solid #fff;
		background: #91cf50;
		cursor: pointer;

		svg {
			align-self: center;
			justify-self: center;
			width: 20px;
			height: 20px;
			fill: #fff;
			transform: rotate(0deg);
			transition: all $transition;
		}

		p {
			display: block;
			margin-block-start: $ind;
			padding-inline-end: $ind;
		}

		.expert-tabs__icon--active {
			transform: rotate(180deg);
		}
	}

	&__heading,
	p {
		padding-inline-start: $ind;
	}

	&__title {
		font-size: calc(100% + 0.4vw);

		font-weight: 700;
		line-height: 150%;
	}

	&__content {
		display: none;
		max-height: 0;
		overflow: hidden;
		transform: translateY(-80px);
		transition: all 0.9s;
	}

	.expert-tabs__content--active {
		transform: translateY(0);
		transition: all 0.9s;
	}
}
