.by-offline {
	position: relative;

	margin-block-start: 129px;
	height: 504px;
	h3 {
		position: absolute;
		inset-block-start: 0;
		inset-inline-start: 0;

		margin: 0;

		text-align: left;
		text-transform: none;
		font-weight: 800;
		font-size: 48px;
		line-height: 110%;

		color: #4f4f4f;
	}

	.slider {
		&-slide {
			&__container {
				display: grid;
				grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
				align-items: center;
				gap: 10px 33px;
				a {
					border: none;
					svg {
						width: 100%;
					}
					img {
						width: 180px;
						height: auto;
					}
				}
			}
		}
		&-navigation {
			display: flex;
			justify-content: end;
			min-height: 75px;
		}

		&-button-prev {
			right: 126px;
			left: revert;

			background: url(../../../img/svg/arrow-circled-left.svg) center
				center/contain no-repeat;

			&::after {
				display: none;
			}
		}

		&-button-next {
			right: 53px;

			background: url(../../../img/svg/arrow-circled-right.svg) center
				center/contain no-repeat;
			&::after {
				display: none;
			}
		}

		&-button-prev,
		&-button-next {
			top: 40%;
			height: 50px;
			width: 50px;
			color: $base-color;
		}

		img {
			margin-block: 0;
		}
	}

	#slider {
		height: 100%;
		width: 100%;

		&-pagination {
			display: flex;
			justify-content: end;
			gap: 0;
			top: 17%;
			bottom: revert;
			transform: translateY(-50%);

			color: transparent;

			.swiper-pagination-bullet {
				margin: 0;
				background-color: $base-color;
			}

			.swiper-pagination-current {
				font-weight: 800;
				font-size: 20px;
				line-height: 100%;
				/* identical to box height, or 20px */

				display: flex;
				align-items: flex-end;
				text-align: center;

				/* yll1 */

				color: #ecbe4c;
			}

			.swiper-pagination-total {
				position: relative;
				padding-block-start: 5px;

				display: flex;
				align-items: start;
				text-align: center;

				font-weight: 900;
				font-size: 10px;
				line-height: 100%;

				color: #ecbe4c;

				&::after {
					position: absolute;
					inset-block-start: 15px;

					display: block;
					content: '';
					height: 4px;
					width: 4px;

					border-radius: 100%;

					background: #ecbe4c;
				}
			}
		}

		.swiper-button {
			&-prev,
			&-next {
				&::after {
					font-size: 15px;
				}
			}
		}

		.swiper-button-prev,
		.swiper-button-next {
			font-size: 10px;
		}
	}
}
