.find-solution {
	display: block;
	padding-block-end: 75px;

	h1 {
		text-align: left;
	}

	&-panel {
		display: grid;
		grid-template-columns: 0.6fr 1.4fr;
		gap: 5rem;

		&__container {
			display: grid;

			grid-template-rows: 1fr 0.2fr;
			align-items: center;
			justify-items: center;
			border: 10px solid #e0e0e0;
			border-radius: 20px;
			height: 135px;
			padding: 23px 45px 8.5px;

			svg {
				fill: #333;
				transition: fill 0.3s ease-in-out;
			}

			&:first-child {
				svg {
					&:hover {
						fill: #219653;
					}
				}
			}

			&:nth-child(2) {
				svg {
					&:hover {
						fill: #df1125;
					}
				}
			}

			svg,
			div {
				cursor: pointer;
			}

			&:first-child {
				grid-template-columns: repeat(3, 1fr);
			}

			&:last-child {
				grid-template-columns: repeat(auto-fit, minmax(84px, 1fr));
				grid-template-rows: 1fr;
				height: auto;

				.find-solution {
					&-panel {
						&__item {
							display: flex;
							flex-direction: column;
							align-items: center;

							width: 74px;
							height: 87.36px;

							cursor: pointer;

							div {
								margin-top: 18px;
							}
						}
					}
				}
			}

			svg {
				max-height: 100%;
				max-width: 100%;
			}

			div {
				font-weight: 500;
				font-size: 18px;
				line-height: 150%;
				/* identical to box height, or 27px */
				white-space: nowrap;

				color: #333333;
			}
		}
	}

	&-inputs {
		display: grid;
		grid-template-columns: 29.22% 36.15% 23.63%;
		gap: 5.48%;

		&__container {
			margin-block-start: 30px;
		}

		&__points {
			display: flex;
			justify-content: space-between;
		}

		input {
			margin-block-start: 14px;
		}

		.find-solution-title {
			display: block;
			padding-block-start: 24px;
		}

		&-group {
			&__other {
				display: flex;
				flex-direction: column;
				align-items: center;
				gap: 20px;
				justify-content: center;
			}
		}

		&__other {
			&-anti-allergenic {
				display: flex;
				align-items: center;
				justify-content: center;

				margin-block-start: 45px;
				width: 100%;
				height: 100%;

				font-weight: 500;
				border: 10px solid #e0e0e0;
				border-radius: 20px;

				cursor: pointer;

				p {
					margin: 0;
				}
			}
			&-cancel {
				display: block;

				a {
					color: #bdbdbd;
				}

				&--mob {
					display: none;
					text-align: right;
				}

				a {
					border-bottom-color: #bdbdbd;
				}
			}
		}
	}

	&-title {
		font-weight: 600;
		font-size: 24px;
		height: 50px;
		line-height: 150%;

		color: #219653;
	}
}

@media screen and (max-width: 1419px) {
	.find-solution {
		&-panel {
			gap: 2rem;
			&-container {
				padding-block-start: 13px;
				padding-inline: 10px;

				svg {
					max-height: 80%;
					max-width: 80%;
				}

				div {
					font-size: 14px;
				}
			}
		}

		&-title {
			font-size: 16px;
		}

		&-inputs {
			grid-template-columns: 29.22% 36.15% 25.63%;
			gap: 4.48%;
			&__points {
				font-size: 14px;
			}

			&__other {
				&-anti-allergenic {
					font-size: 12px;
				}

				&-cancel {
					font-size: 12px;
				}
			}
		}
	}
}

@media screen and (max-width: 1025px) {
	.find-solution {
		&-panel {
			display: flex;
			flex-direction: column;
			gap: 21.19px 0;
			&-container {
				padding-block-start: 13px;
				padding-inline: 10px;

				svg {
					max-height: 80%;
					max-width: 80%;
				}

				div {
					font-size: 14px;
				}
			}
		}

		&-title {
			font-size: 16px;
		}

		&-inputs {
			grid-template-columns: 29.22% 36.15% 25.63%;
			gap: 4.48%;
			&__points {
				font-size: 14px;
			}

			&__other {
				&-anti-allergenic {
					font-size: 12px;
				}

				&-cancel {
					font-size: 12px;
				}
			}
		}
	}
}

@media screen and (max-width: 950px) {
	.find-solution {
		position: relative;
		&-inputs {
			display: block;
			&-group {
				display: block;
			}

			&__other {
				&-cancel {
					&--mob {
						display: block;
					}

					&:last-child {
						display: none;
					}
				}

				&-anti-allergenic {
					height: 70px;
				}
			}
		}
	}
}
