.secondary-menu {
	position: absolute;
	z-index: 20;
	inset-block-start: -200vh;
	inset-inline-start: 0;

	display: flex;
	align-items: center;
	justify-content: space-between;

	padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);

	width: 100%;
	height: 100%;

	background: #333;

	color: #fff;

	transition: all 0.3s ease-in-out;

	&.active {
		inset-block-start: 0;
	}

	&-catalog {
		h3 {
			margin: 0;

			font-weight: 500;
			font-size: 64px;
			line-height: 100%;
			/* or 64px */
		}

		&__lines {
			display: flex;
			gap: 20px;
			align-items: end;
			width: 80px;

			margin-block-start: 50px;

			img {
				height: 100%;
			}

			&-line {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: space-between;
				gap: 20px;

				height: 405px;

				img {
					height: auto;
				}
			}
		}
	}

	&__menu {
		&-row {
			display: flex;
			flex-direction: row;
		}

		&-item {
			display: flex;
			flex-direction: column;
			gap: 20px;

			ul {
				li {
					margin-block: 30px;

					font-weight: 600;
					font-size: 40px;
					line-height: 100%;
					/* identical to box height, or 40px */

					a {
						border: none;

						color: #fff;
					}
				}

				&:nth-child(2) {
					margin-top: 50px;
				}
			}
		}
	}
}
