.where-main {
	position: relative;

	padding-block: 87px 0;
	min-height: 490px;

	color: #fff;

	h1 {
		text-transform: uppercase;

		font-weight: 800;
		font-size: 64px;
		line-height: 110%;

		color: #f2f2f2;
	}

	p {
		max-width: 692px;

		font-weight: 600;
		font-size: 24px;
		line-height: 150%;
	}

	&-nav {
		position: absolute;
		inset-block-end: -70px;

		ul {
			display: flex;
			gap: 20px;

			padding: 0;
		}

		li {
			font-weight: 700;
			font-size: 20px;
			line-height: 110%;
			text-transform: uppercase;

			color: #bdbdbd;

			&::before {
				display: none;
			}
		}

		.active {
			color: green;
		}
	}
}
