.product-cover {
	padding-inline-end: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
	padding-inline-start: clamp(1.875rem, 4.5vw + 0.7rem, 6.063rem);
	padding-block-start: 158px;
	padding-block-end: 91px;
	background: url(../../../img/svg/bg-product.svg) center bottom/cover no-repeat;

	svg {
		width: 106.32px;
	}

	h1 {
		margin-block-start: 65px;
		margin-block-end: 0;

		font-size: 48px;
		font-weight: 700;
		line-height: 110%;
		text-transform: uppercase;

		text-align: left;

		color: #333;
	}

	&-dosage {
		font-size: 24px;
		font-weight: 600;
		line-height: 150%;

		color: #bdbdbd;
	}

	&-btns {
		display: flex;
		gap: 21px;
		margin-block-start: 54px;

		&__insects {
			display: flex;
			gap: 15px;
			align-items: center;

			padding: 25px 11.23px;

			width: 204.651px;
			height: 50px;

			background: #fff;

			border-radius: 25px;

			svg {
				height: 27.14px;
			}
		}
		.allegory {
			width: 50px;
			height: 50px;
		}

		.button {
			width: 351px;

			&:hover {
				.button__arrow {
					transform: translateX(301px) rotate(180deg);
				}
			}
		}
	}
}
