.product-advantages {
	&-point {
		display: flex;
		gap: 67px;
		padding-block: 46px 42px;

		svg {
			max-height: 83px;
		}
	}

	&-gray {
		padding-inline-end: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
		padding-inline-start: clamp(1.875rem, 4.5vw + 0.7rem, 6.063rem);
	}

	&-white {
		padding-inline-end: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);
		padding-inline-start: clamp(1.875rem, 4.5vw + 0.7rem, 6.063rem);

		background: #fff;
	}

	h1 {
		text-align: left;
	}

	&-text {
		display: flex;
		flex-direction: column;
		gap: 20px;
	}

	&-score {
		font-size: 48px;

		font-weight: 700;
		line-height: 110%;
		color: #bdbdbd;
	}

	&-descr {
		font-size: 24px;
		font-weight: 500;

		color: #333;
	}
}
