.expert-replies-main {
	padding-block: 85px 65px;
	padding-inline: clamp(1.875rem, 13.5vw - 1.7rem, 14.438rem);

	h1 {
		text-align: left;

		font-weight: 800;
		font-size: 64px;
		line-height: 110%;

		text-transform: uppercase;

		color: #f2f2f2;
	}

	p {
		font-weight: 600;
		font-size: 24px;
		line-height: 150%;
		/* or 36px */

		color: #ffffff;
	}

	.button {
		margin-top: 24px;
		width: 393px;
		background: #e8edf0;

		a {
			color: #c00000;

			margin: 0 28px;
		}

		&:hover {
			background: #a2d45e;

			.button__arrow {
				transform: translateX(343px) rotate(180deg);
			}
		}
	}
}
